














































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import MathJax3 from '@/components/MathJax3/index.vue'

@Component({
  name: 'DynamicYxbjxjsgNumberParams',
  components: {
    MathJax3
  }
})

export default class extends Vue {
  @Prop({ default: '' }) explainText!: string;
  @Prop({ default: false }) readOnly!: boolean;
  @Prop({ default: 0 }) defaultValue!: number;

 public params:any= [];

 public addField() {
   this.params.push({ value: this.defaultValue, B: '', H: '', d: '' })
 }

 public addFieldWithData(val:number, b:number, h:number, d:number) {
   this.params.push({ value: val, B: b, H: h, d: d })
 }

 public removeField(index) {
   this.params.splice(index, 1)
 }

 public removeAllField() {
   let n = this.params.length
   for (let index = 0; index < n; index++) {
     this.params.splice(0, 1)
   }
 }

 public onParamsDataChange() {
   this.$emit('updateParamsData', this.params)
 }
}
